export default {
	data() {
		return {
			userInfo:[{
				name:"巫宇",
				phone:"15332952873",
				address:"北京市 北京市 西城区 南菜园街88号",
				top:false,
				def:false
			},{
				name:"张三",
				phone:"15332952873",
				address:"北京市 北京市 西城区 南菜园街88号",
				top:false,
				def:false
			},{
				name:"李四",
				phone:"15332952873",
				address:"北京市 北京市 西城区 南菜园街88号",
				top:false,
				def:false
			}],
			show:false,
			curIndex:0,
			curIndex1:0,
			text:"已设为默认",
			text1:"设为默认",
			top:"取消置顶",
			top1:"置顶"
		}
	},
	methods: {
		// 默认
		ctrl(e){
			for(var i=0;i<this.userInfo.length;i++){
				if(i==e.currentTarget.dataset.index){
					this.userInfo[i].def=true
				}else{
					this.userInfo[i].def=false
				}
			}
			
		},
		// 使用
		use(e){
			this.curIndex1=e.currentTarget.dataset.index
		},
		// 置顶
		toTop(e){
			var arr=this.userInfo
			for(var i=0;i<this.userInfo.length;i++){
				if(i==e.currentTarget.dataset.index){
					this.userInfo[i].top=!this.userInfo[i].top
					var obj=arr[i]
					if(this.userInfo[i].top){
						arr.splice(e.currentTarget.dataset.index,1)
						arr.unshift(obj)
					}else{
						arr.splice(e.currentTarget.dataset.index,1)
						arr.push(obj)
					}
				}
			}
			
		}
	}
}
